            modifier="material"
            type="text"
            :size="inputLength"
          >
          </v-ons-input>
        </v-ons-list-item>
        <v-ons-list-header>Your email</v-ons-list-header>
        <v-ons-list-item modifier="material">
          <v-ons-input float 
            placeholder="Email"
            modifier="material"
            type="text"
            :size="inputLength"
          >
          </v-ons-input>
