<script>
export default {
  data () {
    return {
      shareItems: {
        Twitter: {
          name: 'Like me on twitter',
          icon: 'md-twitter',
          url: 'https://twitter.com/JenanHong'
        },
        Facebook: {
          name: 'Like me on facebook',
          icon: 'md-facebook',
          url: 'https://www.facebook.com/sklackmn'
        },
        Google: {
          name: 'Google',
          icon: 'md-linkedin',
          url: 'https://www.linkedin.com/in/jenan-hong/'
        }
      }
      // inputLength: document.getElementById('id-form').offsetWidth / 9,
      // textareaLength: document.getElementById('id-form').offsetWidth / 9
    }
  },
  methods: {
    goToURL (url) {
      window.open(url)
    }
  }
}
</script>

<style>
img {
  width: auto;
  max-height: 100%;
}

ons-card {
  cursor: pointer;
  color: #333;
}

.card__title, .card--material__title {
  font-size: 20px;
}
@media (min-width: 500px) {
    .wrapper-contact {
      display: grid;
      grid-template-columns: minmax(400px, auto);
      padding-right: 4%;
      padding-left: 3%;
    }
}
@media (min-width: 700px) {
    .wrapper-contact {
      display: grid;
	    grid-gap: 10px;
	    grid-template-columns: 1fr 1fr;
	    padding-right: 4%;
	    padding-left: 3%;
    }
}

.form-email {
  padding-right: 3.5%;
	padding-left: 2.5%;
}

.box-contact {
	border: #444;
	border-radius: 5px;
	padding: 20px;
	font-size: 150%;
}

.card {
	display: inline-block;
	width: 100%;
}

.marginB {
  margin-bottom: 0.5em;
}
</style>



